    /*  Admin Dashboard Table */
    .tab-Heading{
        padding-bottom: 0px !important;
        font-size: 15px;
        font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    }
    .search-Box-Table{
        margin: 30px 0px;
    }
    
    
    
    /* create Coupan  */
    .d-coupan-box{
        justify-content: center;
       
        font-weight: 800;
    }
    .d-coupan-heading{
        font-size: 1.5rem;
    }
    
    
    
    /* create project css */
    .box-shadow-box{
        box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
    }
    
    .activity_wrap {
        gap: 10px;
    }
    
    .btn-info {
        cursor: pointer;
    }
    
    .rct-session-wrapper .has-wrapper .has-input {
        border: 1px solid #3d4A9E;
    }
    
    
    .text-theme {
        color:#181D3F !important;
    }
    
    .theme-background {
        background-color: #3d4A9E !important;
    }
    
    .error {
        font-size: 16px !important;
        color: red !important;
        margin-top: -15px;
        margin-bottom: 10px;
        text-align: left;
    }
    
    .edit-form {
        padding: 32px 25px;
        padding-bottom: 5px;
    }
    .update-btn {
        padding: 7px 20px;
        border: none;
        border-radius: 4px;
        cursor: pointer;
    
    }
    
    .profile-block-wrap {
        background-color: rgb(250, 250, 250) !important;
        width: 200px;
        position: absolute;
        top: 10px;
        left: 60px;
    } 
    
    
    .breadcrumb a {
        color: #fff;
    }
    
    .search-row {
        display: flex;
        align-items: center;
        gap: 5px;
    }
    
    .search-input {
        border: none;
        border-bottom: 1px solid #000;
        width: 260px;
        /* height: 40px; */
    }
    
    .paginationDiv .pagination {
        justify-content: end;
        padding: 6px 14px;
    }
    
    
    .model-form .form-control {
        border: none;
        border-bottom: 1px solid #3d4A9E;;
        box-shadow: none;
        border-radius: 0;
        padding: 0;
    }
    
    .model-form .btn-primary , .model-form .MuiButton-contained, .model-form .btn-primary:hover{
        background:#0b3d45 ;
        border-color: #0b3d45;
    }
    
    .model-form label {
        color: #0b3d45;
    }
    
    .userBlockHorizontalDropdown {
        background-color: #181D3F;;
    }
    
    .financialChartPrice, .financialChartPrice > div {
        min-height: 300px !important;
    }
    
    .rotate {
    
        /* Safari */
        -webkit-transform: rotate(-90deg);
        
        /* Firefox */
        -moz-transform: rotate(-90deg);
        
        /* IE */
        -ms-transform: rotate(-90deg);
        
        /* Opera */
        -o-transform: rotate(-90deg);
        
        float: left;
    
    }
    
    .rangeslider {
        margin: 0 !important;
    }
    
    .rangeslider-horizontal {
        height: 6px !important;
        width: 300px !important;
    }
    
    .rangeslider-horizontal .rangeslider__handle{
        width: 17px !important;
        height: 17px !important;
    }
    
    .rangeslider-horizontal .rangeslider__handle:after {
        width: 10px !important;
        height: 10px !important;
        top: 2px !important;
        left: 2px !important;
    }
    
    .rangeslider-horizontal .rangeslider__fill{
        background-color: #0b3d45cf !important;
    }
    
    
